*{
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
  color: white;
  background-color: #21232A;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;

}

.card img {
  max-height: 200px;
  display: block;
  margin: 0 auto;
}